body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
}

p {
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img {
  max-width: 60vw;
  height: auto;
}

.companyBox {
  margin-top: 10vh;
}

.Top {
  margin-top: 15vh;
  margin-bottom: 10vh;
  margin-left: 20vw;
  margin-right: 20vw;
}

.Top h1 {
  font-size: 64px;
  font-style: normal;
  font-weight: bold;
  margin-bottom: 15px;
}

.Top p {
  font-size: 16px;
  margin-right: 140px;
}

hr.introDivider {
  height: 1px;
  opacity: 0.1;
  margin-bottom: 5vh;
}

.companyShowcase {
  margin-top: 5vh;
  margin-right: 20vw;
  margin-left: 20vw;
}

.companyIntro {
  margin-top: 5px;
  margin-bottom: 35px;
}

.companyText {
  margin-top: 35px;
  margin-bottom: 35px;
}
